<template>
    <div >
      <Row :gutter="16">
        <Col span="6">
          <div  class="input-item">
            <span class="input-label">积分编码：</span>
            <InputNumber  :min="1" :step="1" v-model="numID" :formatter="value => `${value}`.replace(/\./g, '')"
                          :parser="value => value.replace(/\./g, '')" class="input-width"></InputNumber>
          </div>
        </Col>
        <Col span="10" >
          <div  class="input-item">
            <daterange :name="'日期'"  @start-time="getStartTime"  @end-time="getEndTime"></daterange>
          </div>
        </Col>
        <Col span="2">
          <div  class="input-item" style="float:right">
            <Button type="primary" @click="exportInfo">导出</Button>
          </div>
        </Col>
      </Row>
    </div>
</template>
<script>
  import daterange from '../components/daterange'
    export default {
      data(){
        return{
          numID:null,
          startTime:'',
          endTime:'',
        }
      },
    mounted() {
      this.$nextTick(function () {
        })
    },
      components: {
        daterange
      },
      //导出数据
      methods: {
        exportInfo(){
          let that=this
          this.$Modal.confirm({
            title: '提示',
            content: `确定导出吗？`,
            onOk: ()=>{
              this.exportOk()
            }
          })
        },
        //导出数据
        exportOk(){
          let objData={
            point_cv:this.numID,
            start_time:this.startTime.length>0?this.startTime.replace(/-/g,''):'',
            end_time:this.endTime.length>0?this.endTime.replace(/-/g,''):'',
            }
          let jsonData = protocol.getJsonParams(protocol.exportYunWeiList, objData);
          http.ajax('', jsonData, true, (data) => {
            window.location.href=loadUrl+data.excel_url
          })
        },
        getStartTime(e){
          this.startTime=e
        },
        getEndTime(e){
          this.endTime=e
        },
      }

    }
</script>

<style scoped>
  @import '../../css/common.css';
</style>



